import { Component, OnInit } from '@angular/core';
import { BasicTables } from '../tables/BasicTables.class';
import { RandomTablesService } from '../tables/random-tables.service';
import { QuotesTables } from '../tables/QuotesTables.class';

@Component({
  selector: 'app-basic-generators',
  templateUrl: './basic-generators.component.html',
  styleUrls: ['./basic-generators.component.scss']
})
export class BasicGeneratorsComponent implements OnInit {

  durationRolls: Array<string>;
  crypticQuote: string;

  constructor() { }

  ngOnInit() {
    this.rollDurations();
  }

  rollDurations() {
    let durationTable = BasicTables.DURATION;
    this.durationRolls = [];
    for(var entry of durationTable) {
      this.durationRolls.push(RandomTablesService.eval(entry[1], {
        "@tables": BasicTables
      }));
    }
  }

  rollCrypticQuotes() {
    let crypticQuote_1 = QuotesTables.QUOTE[0][1];
    this.crypticQuote = RandomTablesService.eval(crypticQuote_1, {
      "@tables": QuotesTables
    })
  }
}



